@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,600;0,700;1,400&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Nunito", sans-serif;font-weight:1400}.player-container{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.player-controller{width:40%;display:flex;align-content:center;justify-content:space-between;padding:1rem;padding-bottom:3rem}.player-controller svg{cursor:pointer}.time-controller{display:flex;width:50%;justify-content:space-between}.time-controller input{width:100%;padding:1rem 0rem;cursor:pointer}.time-controller p{padding:1rem}@media screen and (max-width: 768px){.time-controller{width:80%}.player-controller{width:80%}}.song-container{min-height:70vh;background-color:#f09999;display:flex;flex-direction:column;align-items:center;justify-content:center}.song-container img{width:30%;border-radius:50%}.song-container h1,.song-container h2{padding:1rem;padding-bottom:0}@media screen and (max-width: 768px){.song-container img{width:60%}}.library{position:fixed;top:0;left:0;width:18rem;background-color:coral;box-shadow:2px 2px 50px #8e8e8e;overflow:scroll;height:100%;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:all 0.5s ease}.library h1{padding:2rem;padding-bottom:29px;background-color:whitesmoke}.library-song-container{display:flex;align-items:center;padding:1rem 2rem 1rem 2rem;cursor:pointer}.library-song-container img{width:30%}.library-song-container:hover{background:#bbd0bb}.song-description{padding-left:1rem;font-style:italic;color:#282727}.song-description h3{font-size:1.3rem}.song-description h4{font-size:0.9rem}.active-library{-webkit-transform:translateX(0%);transform:translateX(0%);opacity:1}@media screen and (max-width: 768px){.library{width:100%}}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;border:none;border-radius:1rem;cursor:pointer;padding:0.5rem;border:2px solid #414141;transition:all 0.3s ease;font-size:1.8rem}nav button:hover{background:#414141;color:white}nav button img{height:1.5rem;width:1.5rem}nav .library-active{background:#414141;color:white}@media screen and (max-width: 768px){nav button{z-index:10}}

